import http from '../httpConfig'

const table = 'mrbase_sys_users'

/**
 * @description: 管理员列表
 * @param {interface} /api/systemmanagement/list-user/v1
 * @return: 
 * @author: lwq
 */

export const mrc_admin_list = (params = {}) => {
  params['~table~'] = table
  return new Promise((resolve, reject) => {
    http.post('/api/systemmanagement/list-user/v1', params)
      .then(res => {
        resolve(res)
      })
      .catch(err => {
        reject(err)
      })
  })
}

