<template>
  <div>
    <div v-if="$route.name == 'mrc_system_admin'">
      <mrc_ess_table :headers='headers'
                     pageName='mrc_system_add'
                     :paginationOptions='paginationOptions'
                     @turnPage='getList'
                     :isLoading='loading'
                     :slotKey="['status']"
                     :items='items'>
        <template v-slot:item.actions='{item}'>
          <menu-list :list="[
                    { name: $t('label_View'),type: 'view'},
                    { name: $t('label_Edit'),type: 'edit'},
                     {name: $t('label_To_reset_your_password'),type: 'resetPassword'},
                     {name: item.status ? $t('label_Frozen_account') : $t('label_Frozen_account'),type: 'frozenStatus' }
                     ]"
                     @handleClick='handleClick($event,item)'>
            <template v-slot:showAction>
              <v-icon>mdi-dots-vertical</v-icon>
            </template>
          </menu-list>
        </template>
        <template v-slot:item.status='{item}'>
          <span class="status"
                :class="{'is_used': !item.status}">{{item.status ? $t('label_Working') : $t('label_Frozen')}}</span>
        </template>
      </mrc_ess_table>
      <!-- <router-link :to='{name: "mrc_system_add"}'>新增管理员</router-link>
      <router-link :to='{name: "mrc_system_detail"}'>详情管理员</router-link> -->
    </div>
    <router-view v-else></router-view>
  </div>
</template>

<script>
import { required } from 'vuelidate/lib/validators'
import { mrc_admin_list, mrc_admin_disabled, mrc_admin_reset } from '@/service/api/mrc_system_admin'
export default {
  components: {},
  props: {},
  data () {
    return {
      loading: false,

      items: [],
      paginationOptions: {
        pagesize: this.$store.state.pageSize,
        cpage: this.$store.state.cpage,
        totalCount: 0, //总条数
        totalpage: 0, //总页数
      }
    };
  },
  validations: {},
  computed: {
    headers () {
      return [
        {
          text: this.$t('label_account'),
          value: 'user_account'
        },
        {
          text: this.$t('label_Employee_name'),
          value: 'user_name'
        },
        {
          text: this.$t('label_state'),
          value: 'status'
        },
        {
          text: 'actions',
          value: 'actions'
        }
      ]
    },
  },
  created () { },
  watch: {
    async '$route' (val) {
      if (val.name == 'mrc_system_admin') {
        await this.getList(this.paginationOptions)
        this.$store.commit('setTab', 0)
      }
    }
  },
  async mounted () {
    await this.getList(this.paginationOptions)
  },
  beforeDestroy () {
    this.$store.commit('setCpage', 1)
  },
  methods: {
    /**
     * @description: 获取列表数据
     * @param {type} 
     * @return: 
     * @author: lwq
     */
    async getList (obj) {
      this.loading = true
      const { cpage, pagesize } = obj
      this.paginationOptions = obj
      const res = await mrc_admin_list({ cpage, pagesize })
      if (res.code = 200) {
        this.items = res.data
        this.paginationOptions.totalCount = res.totalCount
        this.paginationOptions.totalpage = res.totalpage
      }
      this.loading = false
    },
    /**
     * @description: 点击menulist的事件
     * @param {type} 
     * @return: 
     * @author: lwq
     */
    async handleClick ({ item, index }, obj) {
      if (item.type == 'view') {
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_subPage_name', obj.user_account)
        this.$store.commit('set_iconstatus', true)
        this.$router.push({
          name: 'mrc_system_detail',
          params: {
            uuid: obj.uuid,
            name: obj.user_account
          }
        })
      }
      if (item.type == 'edit') {
        this.$store.commit('set_uuid', obj.uuid)
        this.$store.commit('set_subPage_name', obj.user_account)
        this.$store.commit('set_iconstatus', false)
        this.$router.push({
          name: 'mrc_system_detail',
          params: {
            uuid: obj.uuid,
            name: obj.user_account
          }
        })
      }
      if (item.type == 'frozenStatus') {
        const disabled = await mrc_admin_disabled({ uuid: obj.uuid, status: obj.status ? 0 : 1 })
        if (disabled.code == 200) {
          this.$store.commit('newsTips', obj.status ? '冻结成功' : "解冻成功")
          await this.getList(this.paginationOptions)
        }
      }
      if (item.type == 'resetPassword') {
        const reset = await mrc_admin_reset({ uuid: obj.uuid })
        if (reset.code == 200) {
          this.$store.commit('newsTips', this.$t('message_The_operation_was_successful'))
          await this.getList(this.paginationOptions)
        }
      }
    }
  },
};
</script>

<style scoped>
</style>
